import React from 'react'
import s from './table.module.css'

const TableBody = ({ currentItems }) => {
  const items = currentItems()

  return (
    <tbody className={s.tableRow}>
      {items.map((item) => (
        <tr key={item.id}>
          <td>{item.datacreat}</td>
          <td>{item.title}</td>
          <td>{item.quantity}</td>
          <td>{item.distance}</td>
        </tr>
      ))}
    </tbody>
  )
}
export default TableBody
