import React, { useContext, useEffect } from 'react'
import './App.css'

import TableFilter from './components/TableFilter'
import TableBody from './components/TableBody'
import TableHead from './components/TableHead'
import Pagination from './components/Pagination'
import { TableContext } from './context/tableContext/TableContext'

function App() {
  // При помощи деструктуризации получаем данные и функцию диспатч из контекста
  const {
    data,
    sortItem,
    currentPage,
    currentItems,
    pageSize,
    setData,
    paginate,
    sortTop,
    sortDown,
    tableFilter,
  } = useContext(TableContext)
  // console.log(data)
  useEffect(() => {
    setData()
  }, [])

  if (!data) {
    return <h1>Подождите идёт загрузка</h1>
  }
  // useEffect(() => {
  //   setData()
  // }, [])
  //Считаем колличество элеменков на странице и узнаём какие именно элементы должны распологаться
  // на этой странице
  // lastItemIndex - самый последний элемент на текущей странице
  // firstItemIndex - самый первый елемент на текущей странице
  // currenyItems - все элементы находящиеся на текущей странице

  // console.log(currentItems)

  return (
    <div className='App'>
      <TableFilter dataLength={data.length} tableFilter={tableFilter} />
      <table>
        <TableHead sortItem={sortItem} sortTop={sortTop} sortDown={sortDown} />
        <TableBody data={data} currentItems={currentItems} />
      </table>
      <Pagination
        totalItems={data.length}
        pageSize={pageSize}
        currentPage={currentPage}
        paginate={paginate}
      />
    </div>
  )
}

export default App
