import React, { useReducer } from 'react'

import { tableReducer } from './tableReducer'
import { API } from '../../api'
import { GET_DATA, PAGINATE, SORT_TOP, SORT_DOWN, FILTER } from '../types'
import { TableContext } from './TableContext'
import { data } from '../../data'

export const TableState = ({ children }) => {
  const initialState = {
    data: null,
    currentPage: 1,
    pageSize: 7,
    sortItem: null,
  }
  const [state, dispatch] = useReducer(tableReducer, initialState)

  const setData = async () => {
    // const data = await API.fetchData()
    dispatch({ type: GET_DATA, data })
  }

  const tableFilter = (columnName, condition, inputValue) =>
    dispatch({ type: FILTER, columnName, condition, inputValue })

  const sortTop = (item) => dispatch({ type: SORT_TOP, item })
  const sortDown = (item) => dispatch({ type: SORT_DOWN, item })

  const lastItemIndex = state.currentPage * state.pageSize
  const firstItemIndex = lastItemIndex - state.pageSize
  const currentItems = () => state.data.slice(firstItemIndex, lastItemIndex)
  const paginate = (page) => dispatch({ type: PAGINATE, page })

  return (
    <TableContext.Provider
      value={{
        data: state.data,
        sortItem: state.sortItem,
        pageSize: state.pageSize,
        currentPage: state.currentPage,
        currentItems,
        setData,
        sortTop,
        sortDown,
        paginate,
        tableFilter,
      }}
    >
      {children}
    </TableContext.Provider>
  )
}
